import NavLinks from './NavLinks'
import { useSelector } from 'react-redux'

import './Navbar.css'
import { useHistory } from 'react-router'
import SearchPanel from './SearchPanel'


function Navbar() {
    let history = useHistory()

    const state = useSelector((state) => state)

    const handleAddress = (path) => {
        history.push(`/${path}`)
    }
    
    let countCart = 0

    if (state.cart.length) {
        state.cart.map(item => countCart += item.count)
    }

    return (
        <>
            <nav className="navbar navbar-light">
                <div className="container-fluid">
                    <div onClick={()=>handleAddress('')} className='logo'></div>

                    <SearchPanel/>

                    <div className='wrapper-totalPrice'>
                        <button type="button" className="btn btn-sm btn-link position-relative" onClick={()=>handleAddress('compare')}>
                            <img src='data:image/svg+xml;base64,' width="55" alt="" />
                                <span className="badge rounded-pill position-compare">{state.comparison.length}</span>
                        </button>
                        
                        <button type="button" className="btn btn-sm btn-link position-relative" onClick={()=>handleAddress('basket')}>
                            <img src="https://img.icons8.com/external-kiranshastry-gradient-kiranshastry/64/000000/external-shopping-cart-ecommerce-kiranshastry-gradient-kiranshastry-1.png" width="55" alt=''/>
                                <span className="badge rounded-pill">{countCart}</span>
                        </button>
                        <span className='text-white'>{state.totalPrice},00$</span>  
                    </div>
                </div>
            </nav>
            <NavLinks/>
        </>
    )
}

export default Navbar
